@font-face {
	font-family: "iransans";
	src: url("../fonts/ir_sans.ttf");
	font-weight: normal;
}
@font-face {
	font-family: "iransans";
	src: url("../fonts/ir_sans_b.ttf");
	font-weight: bold;
}

:root {
	--text-white: #f4f4f4;
	--text-black: #404040;
	--text-grey: #545454;
	--text-greyer: #707070;
	--text-green: #00b67a;
	--grey: #e5e5e5;
	--bg-grey: #f5f7fa;
	--blue: #265cb7;
	--dim-blue: #e9f1ff;
	--blue-shadow: #265bb730;
}
* {
	box-sizing: border-box;
	margin: 0;
	padding: 0;
	font-family: "iransans", "Roboto", sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}
body {
	position: relative;
	margin: 0;
	background-color: var(--bg-grey);
	padding: 0;
	color: var(--text-black);
}
a {
	text-decoration: none;
	color: inherit;
}
/* Handling different width sizes */
/* Any device smaller than 600px */
.col-xs-1 {
	width: 8.33%;
}
.col-xs-2 {
	width: 16.66%;
}
.col-xs-3 {
	width: 25%;
}
.col-xs-4 {
	width: 33.33%;
}
.col-xs-5 {
	width: 41.66%;
}
.col-xs-6 {
	width: 50%;
}
.col-xs-7 {
	width: 58.33%;
}
.col-xs-8 {
	width: 66.66%;
}
.col-xs-9 {
	width: 75%;
}
.col-xs-10 {
	width: 83.33%;
}
.col-xs-11 {
	width: 91.66%;
}
.col-xs-12 {
	width: 100%;
}
/* Small devices (portrait tablets and large phones, 600px and up) */
@media only screen and (min-width: 600px) {
	.col-s-1 {
		width: 8.33%;
